import Vue from 'vue'
import VueRouter from 'vue-router'
// 
import Home from '@/views/Home/Home'
// 
import Sort from '@/views/Sort/Sort'
// 
import Cart from '@/views/Cart/Cart'
// 
import Mine from '@/views/Mine/Mine'
//  
import Login_tel from '@/views/Login/Login_tel'
import Login_pwd from '@/views/Login/Login_pwd'
// 
import Reg from '@/views/Reg/Reg'
// 
import List from '@/views/List/List'
// 
import Detail from '@/views/Details/Detail'

Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    routes: [{
        path: '/',
        name: 'home',
        component: Home
    }, {
        path: '/home',
        redirect: {
            name: 'home'
        }
    }, {
        path: '/sort',
        name: 'sort',
        component: Sort
    }, {
        path: '/cart',
        name: 'cart',
        component: Cart
    }, {
        path: '/mine',
        name: 'mine',
        component: Mine
    }, {
        path: '/login_tel',
        name: 'login_tel',
        component: Login_tel
    }, {
        path: '/login_pwd',
        name: 'login_pwd',
        component: Login_pwd
    }, {
        path: '/reg',
        name: 'reg',
        component: Reg
    }, {
        path: '/detail',
        name: 'detail',
        component: Detail
    }, {
        path: '/list',
        name: 'list',
        component: List
    }]
});