<template>
  <div>
    <van-nav-bar :fixed="true" title="" left-text=" " left-arrow @click-left="onClickLeft">
      <van-icon name="more-o" :size="26" slot="right" />
    </van-nav-bar>
    <div class="content">
      <div class="cont">
        <!--  -->
        <div class="swiperImg">
          <van-swipe @change="onChange">
            <van-swipe-item v-for="(item, index) in images" :key="index">
              <img v-lazy="item.image" width="100%" />
            </van-swipe-item>
            <div class="custom-indicator" slot="indicator">{{ current + 1 }}/2</div>
          </van-swipe>
        </div>
        <!--  -->
        <div class="tit_price" v-for="(item,index) in detail" :key="index" :data-id="item.gid">
          <p class="tit" v-text="item.subject"></p>
          <p class="description"> </p>
          <div class="price_box">
            <div class="price">
              <span class="pro_price">
                
                <span class="sale_pri" v-text="item.sale_price"></span>
                <del>
                  
                  <span class="origin_pri">135.40</span>
                </del>
              </span>
              <span class="pro_origin">
                <a>
                  <img
                    src="https://static.epetbar.com/static_wap/appmall/lib/goods/quality_assurance.png"
                    alt
                  />
                </a>
              </span>
            </div>
          </div>
        </div>
        <!-- promotion  -->
        <div class="promotion">
          <div class="promo clearfix">
            <div class="text"></div>
            <div class="rela">
              <a>
                <span class="icon"></span>
                <span class="title">506</span>
              </a>
            </div>
          </div>
        </div>
        <!--  -->
        <div>
          <div class="mould_4">
            <a>
              <span>
                <i>
                  <img
                    src="https://static.epetbar.com/static_wap/appmall/lib/goods/details_tag.png"
                    alt
                  />
                </i>
                <span class="fcol3"></span>
              </span>
              <span class="tit fcol9"></span>
            </a>
          </div>
        </div>
        <!--  -->
        <div>
          <div class="handcursor">
            <a>
              <span class="fcol3" style="width:30%"></span>
              <span class="fcol9" style="width:65%;text-align:right">3.9</span>
            </a>
          </div>
        </div>
        <!-- specifications  -->
        <div>
          <ul class="specifications">
            <li>
              <span></span>
              <span style="color:#ea5858;">'2kg'</span>
            </li>
            <li>
              <span class="tit"></span>
              <div>
                <span>2kg</span>
              </div>
            </li>
          </ul>
        </div>
        <!--  -->
        <div>
          <div class="buy_num">
            <span></span>
            <van-stepper v-model="value" :min="1" :max="10" integer />
          </div>
          <!--  -->
          <ul class="address bgfff bbt">
            <a href="https://wap.epet.com/place.html?fw=0">
              <li class="paddingtb10 ft13 rela arrow c333 ml10">
                <span class="ellipsis w60 pr10 handcursor">
                  <span></span>
                  <i class="icon iconfont">
                    <img
                      src="//static.epetbar.com/static_wap/epetapp/pages/goods_detail/images/dress.png"
                      class="ingb"
                    />
                  </i>
                  <span> </span>
                </span>
                <span class="ellipsis w40 pr20 ftr c999">
                  <span class="hub b-all round5 ft12">E</span>
                </span>
              </li>
            </a>
          </ul>
        </div>
        <!--  -->
        <div>
          <div class="mould_9">
            <a>
              <div class="bgfff describe-ul ft12 c666">
                <span class="tag-item pt10 pb10">
                  <img
                    src="https://static.epetbar.com/static_wap/appmall/lib/goods/qualityassurance.png"
                    alt
                  />
                  <span></span>
                  <span></span>
                </span>
                <span class="tag-item pt10 pb10">
                  <img
                    src="https://static.epetbar.com/static_wap/appmall/lib/goods/freeshipping.png"
                    alt
                  />
                  <span></span>
                  <span>99</span>
                </span>
                <span class="tag-item pt10 pb10">
                  <img
                    src="https://static.epetbar.com/static_wap/appmall/lib/goods/thirtydays.png"
                    alt
                  />
                  <span></span>
                  <span>30</span>
                </span>
              </div>
            </a>
          </div>
        </div>
      </div>
      <div class="footer clearfix">
        <van-goods-action>
          <van-goods-action-icon icon="star-o" text="" @click="onClickIcon" />
          <van-goods-action-icon icon="cart-o" :info="info" text="" @click="onClickIcon" />
          <van-goods-action-button type="warning" text="" @click="onClickButton" />
          <van-goods-action-button type="danger" text="" @click="onClickButton" />
        </van-goods-action>
      </div>
    </div>
  </div>
</template>
<script>
import handleCookie from "@/util/cookie/handleCookie"
export default {
  data() {
    return {
      info: 0,
      // 
      images: [],
      // 
      detail:[],
      current: 0,
      value: 1,
      gid:''
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name:'list'
      });
    },
    onClickIcon() {
      this.$router.push({
        name:'cart'
      });
    },
    onClickButton() {
      this.info = this.info * 1 + this.value * 1;
      // const prev = this.common.getCookie('gid')
      handleCookie.goods(
        "ADD",{
          gid:this.gid,
          buyNum:this.info
        }
      )
    },
    onChange(index) {
      this.current = index;
    }
  },
  async created() {
    this.gid = this.$store.getters.getGid;
    let data = await this.$axios('https://www.easy-mock.com/mock/5d47e6b9d7bb1d0fc358c102/menus/p'+this.gid)
    this.images = data.data.datas[0].photos;
    let data1 = await this.$axios('https://www.easy-mock.com/mock/5d47e6b9d7bb1d0fc358c102/menus/'+this.gid)
    this.detail = data1.data.list;
  },
  mounted() {
    const goods = handleCookie.goods("SELECT",{
      type:"ALL"
    });
    let num = '';
    for(let item of goods) {
      num += item.buyNum;
    }
    if(num) {
      this.info = num;
    }
  }
  
};
</script>
<style lang="scss" scoped>
@import '@/css/detail.scss'
</style>
