import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

// 
const store = new Vuex.Store({
    state: {
        gid: '',

    },
    mutations: {
        setGid(state, id) {
            state.gid = id;
        }
    },
    actions: {
        set(context, id) {
            // actioncommit
            context.commit('setGid', id)
        }
    },
    getters: {
        getGid(state) {
            return state.gid;
        }
    }
});

export default store